___CSS_LOADER_EXPORT___.push([module.id, `.parameter-panel-parameters {
  padding: 1.25rem;
  /* Adjust height to account for footer */
  height: calc(100vh - 280px); /* Increased from 200px to give more space from footer */
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.3) transparent;
  position: relative; /* Add this */
}

/* Custom scrollbar for webkit browsers */
.parameter-panel-parameters::-webkit-scrollbar {
  width: 6px;
}

.parameter-panel-parameters::-webkit-scrollbar-track {
  background: transparent;
}

.parameter-panel-parameters::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.parameter-item {
  margin-bottom: 2rem;
}

.parameter-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.param-name {
  color: #FFF;
  font-family: "Helvetica Neue";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5rem;
}

.param-value {
  color: #FFF;
  font-family: "Helvetica Neue";
  font-size: 1rem;
  font-weight: 400;
  padding: 0.375rem 0.75rem;
  border-radius: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
}

/* Custom Slider Styles */
.slider-container {
  width: 100%;
  height: 0.5rem;
  position: relative;
  cursor: pointer;
  padding: 0.75rem 0;
}

.slider-track {
  width: 100%;
  height: 0.5rem;
  border-radius: 1rem;
  background: rgba(255, 255, 255, 0.1);
}

.slider-fill {
  position: absolute;
  top: 0.75rem;
  height: 0.5rem;
  border-radius: 1rem 0 0 1rem;
  background: #9E9E9E;
}

.slider-fill.active {
  background: #101828;
}

.slider-thumb {
  position: absolute;
  top: 50%;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background: #FFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.slider-thumb.active {
  width: 1.5rem;
  height: 1.5rem;
  background: #FFF;
  border: 2px solid #101828;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25),
             0px 0px 0px 4px rgba(255, 189, 98, 0.15);
}

.parameter-range {
  display: flex;
  justify-content: space-between;
  margin-top: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
  font-family: "Helvetica Neue";
  font-size: 0.875rem;
}

/* Custom Dropdown Styles */
.custom-dropdown {
  width: 100%;
  position: relative;
  margin-top: 0.25rem;
}

.dropdown-header {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.25rem;
  padding: 0.375rem 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  height: 24px;
}

.dropdown-value {
  color: white;
  font-size: 0.875rem;
}

.dropdown-arrows {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.arrow {
  color: rgba(255, 255, 255, 0.6);
  font-size: 6px;
  line-height: 1;
}

.dropdown-options {
  position: absolute;
  /* Change positioning for bottom dropdowns */
  bottom: calc(100% + 2px); /* This will make dropdowns open upwards when near bottom */
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.25rem;
  padding: 0.25rem;
  z-index: 1100; /* Increased z-index to appear above footer */
  display: none;
}

.dropdown-options.open {
  display: block;
}

.dropdown-options.open-upward {
  bottom: calc(100% + 2px);
  top: auto;
}

.dropdown-options.open-downward {
  top: calc(100% + 2px);
  bottom: auto;
}

.dropdown-option {
  padding: 0.375rem 0.5rem;
  color: white;
  cursor: pointer;
  border-radius: 0.125rem;
  font-size: 0.875rem;
}

.dropdown-option:hover {
  background: rgba(255, 255, 255, 0.1);
}

.dropdown-option.selected {
  background: rgba(255, 255, 255, 0.15);
}

/* Remove all Mantine styles */
.mantine-Select-dropdown,
.mantine-Select-item,
.mantine-Select-rightSection {
  display: none;
}
