___CSS_LOADER_EXPORT___.push([module.id, `.configurator-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: transparent; /* Make it transparent */
}

.configurator-content {
  display: flex;
  height: 100%;
  width: 100%;
  position: relative;
  padding: 1rem;
  gap: 1rem;
}

.viewer-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 80%; /* Reduced from 90% */
  height: 100%;
  padding: 1rem;
}

/* New styles for the Share button */
.share-button-container-configurator {
  position: absolute;
  top: 80px; /* Position under header */
  right: 500px; /* Align to right side */
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.share-button-configurator {
  background: rgba(255, 255, 255, 0.3);
  border: none;
  border-radius: 50px;
  padding: 8px 20px;
  color: #ffffff;
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.share-button-configurator:hover {
  background: rgba(255, 255, 255, 0.4);
}

.action-buttons {
  position: absolute;
  bottom: 100px; /* Reduce the distance from the bottom */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 16px;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px 20px;
  border-radius: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Add a soft shadow for depth */
}
  
.parameter-panel-container {
  width: 400px; /* Fixed width */
  height: calc(100vh - 2rem); /* Full height minus padding */
  margin: 1rem;
  border-radius: 1.5rem;
  background: rgba(25, 25, 25, 0.5);
  backdrop-filter: blur(50px);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  margin-right: 5rem;
  margin-bottom: 70rem;
  margin-top: 6rem;
}


/* Tab navigation styles */
.parameter-tabs {
  display: flex;
  padding: 0.75rem;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.10);
  gap: 0.5rem;
}

.tab-button {
  display: flex;
  padding: 0.375rem 0.75rem;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  flex: 1 0 0;
  border-radius: 0.5rem;
  font-family: "Helvetica Neue";
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.375rem;
  color: #DDD;
  transition: all 0.3s ease;
}

.tab-button.active {
  height: 2.125rem;
  padding: 0.5rem 1.3125rem;
  border-radius: 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.40);
  background: #151515;
  box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.10);
  backdrop-filter: blur(50px);
  color: #FFF;
}
  
.configurator-podium::after {
  content: '';
  width: 80%;
  height: 10px;
  background: #333333;
  border-radius: 50%;
  position: absolute;
  bottom: 0;
  z-index: -1; /* Place it behind the bike */
}

.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-spinner p {
  margin-top: 1rem;
  color: #666;
}
  
  
@media (max-width: 1200px) {
.configurator-content {
    flex-direction: column;
  }

  .parameter-panel-container {
    width: 100%;
    height: auto;
    border-left: none;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
  }
}

.export-menu-wrapper {
  margin-top: 8px;
  width: 300px;
}
