___CSS_LOADER_EXPORT___.push([module.id, `.parameter-panel {
  width: 380px;
  height: auto;
  display: flex;
  flex-direction: column;
  border-radius: 1.5rem;
  overflow: hidden;
  padding: 1rem;
}

/* Tab Navigation */
.tab-navigation {
  display: flex;
  padding: 0.5rem;
  align-items: center;
  gap: 0.5rem;
  border-radius: 2rem;
  margin-bottom: 1.5rem;
}

.tab-button {
  flex: 1;
  padding: 0.5rem 1rem;
  border-radius: 1.5rem;
  color: rgba(255, 255, 255, 0.7);
  font-family: "Helvetica Neue";
  font-size: 6rem;
  font-weight: 500;
  border: none;
  background: transparent;
  transition: all 0.3s ease;
  cursor: pointer;
  text-transform: capitalize; /* Capitalizes first letter */
  letter-spacing: 0.02em; /* Slight letter spacing for better readability */
}

.tab-button:hover {
  color: rgba(255, 255, 255, 0.9);
}

.tab-button.active {
  background: #000;
  color: #FFF;
  font-weight: 600; /* Slightly bolder when active */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */


}

/* Shape Grid */
.shapes-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 0.75rem;
  margin: 1rem 0;
}

.shape-item {
  aspect-ratio: 1;
  border-radius: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

/* Color Grid */
.color-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0.5rem;
  margin: 1rem 0;
}

.color-circle {
  aspect-ratio: 1;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid transparent;
}

.color-circle.selected {
  border-color: #fff;
}

/* Section Labels */
.section-label {
  color: #fff;
  font-size: 0.875rem;
  margin: 1rem 0 0.5rem 0;
}

/* Material Options */
.material-options {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin: 1rem 0;
}

.material-option {
  padding: 1rem;
  border-radius: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
