___CSS_LOADER_EXPORT___.push([module.id, `.export-menu {
  position: absolute;
  top: 120px; /* Adjusted to appear under the share button */
  left: 100px; /* Aligned with the share button */
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  padding: 16px;
  width: 100%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.export-menu-content {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.export-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-size: 14px;
}

.export-dropdown {
  background: rgba(34, 34, 34, 0.2);
  border-radius: 6px;
  padding: 5px 8px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.chevron-down {
  width: 16px;
  height: 16px;
  background-size: contain;
  background-repeat: no-repeat;
}

.toggle-switch {
  width: 38px;
  height: 22px;
  background: #A6A6A6;
  border-radius: 13.5px;
  position: relative;
}

.toggle-switch::after {
  content: '';
  position: absolute;
  width: 18px;
  height: 18px;
  background: white;
  border-radius: 50%;
  top: 2px;
  left: 2px;
  transition: 0.3s;
}

.export-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.close-button {
  margin-top: 8px;
  opacity: 0.8;
}

.close-button:hover {
  opacity: 1;
}

/* Style the Mantine components to match the theme */
.export-menu :global(.mantine-Button-root) {
  background: rgba(255, 255, 255, 0.3) !important;
  border: none !important;
  border-radius: 50px !important;
  padding: 8px 20px !important;
  color: #ffffff !important;
  font-family: 'Inter', sans-serif !important;
  font-size: 16px !important;
  font-weight: 400 !important;
  cursor: pointer !important;
  transition: background-color 0.3s ease !important;
  height: auto !important;
}

.export-menu :global(.mantine-Button-root:hover) {
  background: rgba(255, 255, 255, 0.4) !important;
}

.export-menu :global(.mantine-Select-input) {
  background: rgba(255, 255, 255, 0.2) !important;
  border: 1px solid rgba(255, 255, 255, 0.3) !important;
  border-radius: 6px !important;
  color: white !important;
  font-family: 'Inter', sans-serif !important;
}

.export-menu :global(.mantine-Select-label) {
  color: white !important;
  font-family: 'Inter', sans-serif !important;
  margin-bottom: 8px !important;
}
